.search-message {
  margin: 100px 0px;
  font-size: 48px;
  text-align: center;
  color: #cccccc; }
  @media only screen and (max-width: 650px) {
    .search-message {
      margin: 50px 0px;
      font-size: 32px; } }

.search-loading {
  position: relative;
  height: 200px; }
  .search-loading:before, .search-loading:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 4px solid #e96e50;
    opacity: 1;
    border-radius: 50%;
    animation: ripple-effect 1s cubic-bezier(0, 0.2, 0.8, 1) infinite; }
  .search-loading:after {
    animation-delay: -0.5s; }

@keyframes ripple-effect {
  0% {
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    width: 100px;
    height: 100px;
    opacity: 0; } }
